.map-container {
  position: fixed;
  top: 0;
  background-color: rgba(0,0,0,0.5);
  width: 100%;
  height: 100%;
  padding: 30px 30px 0 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.map-bg {
  position: relative;
  height: 450px;
  background-color: #fff;
  padding: 50px 20px 50px 20px;
}
.close-map {
  cursor: pointer;
  position: absolute;
  top: 15px;
  right: 25px;
}
.table-line{
  margin-bottom:20px;
  margin-left:30px;
}
.sub-title{
  font-size:14px;
}
.pic-line{
  margin-left:100px;
}
.pic{
  width:300px;
  height: auto;
  margin-bottom: 10px;
}
.pic+.pic{
  margin-left:10px !important;
}
.pic:nth-child(3n+1){
  margin-left:50px !important;
}
li {list-style-type:none;
}
.el-form-item {
  margin-bottom: 20px;
}
.el-upload-list--picture .el-upload-list__item-thumbnail {
  width: 163px;
  height: 71px;
}
.el-checkbox{
  margin-right:30px;
}
.el-checkbox+.el-checkbox{
  margin-left:0;
}
.build-name{
  position: relative;
}
.search-address-list{
  height: 300px;
  width: 50%;
  background-color: #fff;
  position: absolute;
  top: 50px;
  left:150px;
  z-index:10;
  overflow: hidden;
  overflow-y: scroll;
  border:1px solid #dcdfe6;
  padding:10px;
  cursor:pointer;
}
.address1{
  color:#333;
  font-size:14px;
}
.address2{
  color:#999;
  font-size:12px;
}
#allmap{
  width:80%;
  height: 300px;
  margin:0 0 20px 150px;
}
.city,.area,.address,.building-name{
  position: relative;
}
.city:before,.area:before,.address:before{
  position: absolute;
  left:90px;
  top:11px;
  display:inline-block;
  content: "*";
  color:red;
}
.building-name:before{
  left:22px;
}
.area:before{
  left:75px;
}
.search-result:before{
  left:32px;
}



